<template>
  <div class="index">
    <div class="imp-block">
      <a download  class="f-small main-c mr5" href="../gy-files/template/高中成绩基础数据导入模板.xlsx">下载模板</a>
      <label for="importDom">
        <div class="btn-main btn-upload mr15 ml5">批量导入</div>
      </label>
      <input accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
             @change="upload($event)" ref="upload" id="importDom" type="file" hidden>

    </div>
    <search-box :searchData="searchData" :table="table"></search-box>
    <div class="table-box">

      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th>班级</th>
          <th>{{table.query.discipline}}名次</th>
          <th>{{table.query.discipline}}优秀(50%)</th>
          <th>{{table.query.discipline}}良好(30%)</th>
          <th>操作</th>
        </tr></thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>{{item.className}}</td>
          <td v-show="table.query.discipline === i.type" v-for="i in tdData">
            <p v-show="item.status !== true">{{item[i.value]}}</p>
            <el-input-number class="input" v-model="form[i.value]" v-show="item.status === true" size="small" controls-position="right" :min="1" :max="1000000"></el-input-number>
          </td>
          <td>
            <span v-show="item.status !== true" @click="showForm(item,key)" class="oper">编辑</span>
            <span v-show="item.status === true" @click="editFun(item,key)" class="oper">完成</span>
            <span v-show="item.status === true" @click="clearFun(item,key)"class="oper">取消</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
    </div>
  </div>

</template>
<script>
  const thData = [
    {label: '总分', value: '总分'},
    {label: '语文', value: '语文'},
    {label: '数学', value: '数学'},
    {label: '英语', value: '英语'},
    {label: '物理', value: '物理'},
    {label: '化学', value: '化学'},
    {label: '政治', value: '政治'},
    {label: '历史', value: '历史'},
    {label: '生物', value: '生物'},
    {label: '地理', value: '地理'}
  ]
  const tdData = [
    {type: '总分', value: 'totalRank'},
    {type: '总分', value: 'totalExcellentNum'},
    {type: '总分', value: 'totalWellNum'},

    {type: '语文', value: 'languageRank'},
    {type: '语文', value: 'languageExcellentNum'},
    {type: '语文', value: 'languageWellNum'},

    {type: '数学', value: 'mathRank'},
    {type: '数学', value: 'mathExcellentNum'},
    {type: '数学', value: 'mathWellNum'},

    {type: '英语', value: 'englishRank'},
    {type: '英语', value: 'englishExcellentNum'},
    {type: '英语', value: 'englishWellNum'},

    {type: '物理', value: 'physicsRank'},
    {type: '物理', value: 'physicsExcellentNum'},
    {type: '物理', value: 'physicsWellNum'},

    {type: '化学', value: 'chemistryRank'},
    {type: '化学', value: 'chemistryExcellentNum'},
    {type: '化学', value: 'chemistryWellNum'},

    {type: '政治', value: 'politicsRank'},
    {type: '政治', value: 'politicsExcellentNum'},
    {type: '政治', value: 'politicsWellNum'},

    {type: '历史', value: 'historyRank'},
    {type: '历史', value: 'historyExcellentNum'},
    {type: '历史', value: 'historyWellNum'},

    {type: '生物', value: 'biologyRank'},
    {type: '生物', value: 'biologyExcellentNum'},
    {type: '生物', value: 'biologyWellNum'},

    {type: '地理', value: 'geographyRank'},
    {type: '地理', value: 'geographyExcellentNum'},
    {type: '地理', value: 'geographyWellNum'},

  ]
  export default {
    data() {
      return {
        searchData: [
          {type: 'select',title: '学科', notRemove: true, model: 'discipline', option: thData},
          {type: 'input', placeholder: '班级'}
        ],
        tdData: tdData,
        table: {
          url: '/score/queryScoreBaseSeniorPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            discipline: '总分',
            keywords: ''
          }
        },
        form: {

        }
      }
    },
    methods: {
      // 导入
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        this.$refs.upload.value = null;
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/import/importSeniorScoreBase.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        this.$remind('导入成功')
        this.$search(this.table)
      },
      // 展示编辑表单
      showForm(item,key){
        this.tdData.forEach(e=>{
          this.form[e.value] = item[e.value]
        })
        this.form.id = item.id
        this.form.classId = item.classId
        item.status = true
        this.table.list.splice(key,1,item)
      },
      // 修改数据
      async editFun(item,key){
        await this.$ajax({
          url: '/score/updateScoreBaseSenior.do',
          query: this.form
        })
        item.status = false
        this.table.list.splice(key,1,item)
        this.$remind('修改成功')
        this.$search(this.table)
      },
      // 取消修改
      clearFun(item,key){
        item.status = false
        this.table.list.splice(key,1,item)
      }
    },
    mounted(){
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .index
    .imp-block
      position: absolute
      top: 24px
      line-height: 32px
      right: 0
      a
        vertical-align: top
        display: inline-block
        line-height: 30px
        margin-top: 2px

</style>
