<template>
  <div class="index">
    <div class="imp-block">
      <a download  class="f-small main-c mr5" href="../gy-files/template/初中成绩基础数据导入模板.xlsx" >下载模板</a>
      <label for="importDom">
        <div class="btn-main btn-upload mr15 ml5">批量导入</div>
      </label>
      <input accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
             @change="upload($event)" ref="upload" id="importDom" type="file" hidden>

    </div>
    <search-box :searchData="searchData" :table="table"></search-box>

    <resize-table>
    <table class="table">
      <thead>
      <tr>
        <th width="15%">班级</th>
        <th width="15%">A等人数(40%)</th>
        <th width="15%">班平</th>
        <th width="20%">A等目标百分比</th>
        <th width="20%">中考全A目标</th>
        <th width="15%">操作</th>
      </tr></thead>
      <tbody>
      <tr v-for="(item,key) in table.list">
        <td>
          <p>{{item.className}}</p>
        </td>
        <td>
          <p v-show="item.status !== true">{{item.anum}}</p>
          <el-input-number class="input" v-model="form.anum" v-show="item.status === true" size="small" controls-position="right" :min="1" :max="1000000"></el-input-number>
        </td>
        <td>
          <p v-show="item.status !== true">{{item.classAvg}}</p>
          <el-input-number class="input" v-model="form.classAvg" v-show="item.status === true" size="small" controls-position="right" :min="1" :max="1000000"></el-input-number>
        </td>
        <td>
          <p v-show="item.status !== true">{{item.levelARate}}</p>
          <el-input-number class="input" v-model="form.levelARate" v-show="item.status === true" size="small" controls-position="right" :min="1" :max="1000000"></el-input-number>
        </td>
        <td>
          <p v-show="item.status !== true">{{item.seniorExamTarget}}</p>
          <el-input-number class="input" v-model="form.seniorExamTarget" v-show="item.status === true" size="small" controls-position="right" :min="1" :max="1000000"></el-input-number>
        </td>
        <td>
          <span v-show="item.status !== true" @click="showEditForm(item,key)" class="oper">编辑</span>
          <span v-show="item.status === true" @click="editFun(item,key)" class="oper">完成</span>
          <span v-show="item.status === true" @click="clearFun(item,key)"class="oper">取消</span>
        </td>
      </tr></tbody>
    </table>
    </resize-table>
  </div>

</template>
<script>
  export default {
    data() {
      let checkTotal = (type)=>{
        return type.label !== '总分'
      }
      return {
        searchData: [
          {type: 'select', title: '学科', model: 'course', notRemove: true, option: this.$enums.CulturalCourseName.list.filter(checkTotal)},
          {type: 'input', placeholder: '班级'},
        ],
        table: {
          url: '/score/queryScoreBasePage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            keywords: '',
            course: 'LANGUAGE'
          }
        },
        form:{
          id: '',
          classId: '',
          anum: '',
          classAvg: '',
          levelARate: '',
          seniorExamTarget: '',
        }
      }
    },
    methods: {
      // 获取科目
      // 导入
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        this.$refs.upload.value = null;
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/import/importJuniorScoreBase.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        this.$remind('导入成功')
        this.$search(this.table)
      },
      // 展现编辑表单
      showEditForm(item,key){
        this.form = {
          id: item.id,
          className: item.className,
          classId: item.classId,
          anum: item.anum,
          classAvg: item.classAvg,
          levelARate: item.levelARate,
          seniorExamTarget: item.seniorExamTarget,
        }
        item.status = true
        this.table.list.splice(key,1,item)
      },
      // 修改数据
      async editFun(item,key){
        await this.$ajax({
          url: '/score/updateScoreBase.do',
          query: this.form
        })
        item.status = false
        this.table.list.splice(key,1,item)
        this.$remind('修改成功')
        this.$search(this.table)
      },
      // 取消修改
      clearFun(item,key){
        item.status = false
        this.table.list.splice(key,1,item)
      }
    },
    mounted(){
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .index
    .imp-block
      position: absolute
      top: 24px
      line-height: 32px
      right: 0
      a
        vertical-align: top
        display: inline-block
        line-height: 30px
        margin-top: 2px
    .table
      width: calc(100% - 15px)
      td
        padding: 0 10px
      .input
        width: 100%

</style>
