<template>
  <div class="index">
    <div class="search-box">
      <div class="item">
        <span class="text">考试批次</span>
        <el-cascader @change="getDetail" class="cascader"
                     v-model="batchModel" :options="courseCascadeList"></el-cascader>
      </div>
    </div>
    <resize-table>
    <table class="table mt20 mb30">
      <thead>
      <tr>
        <th width="20%">科目</th>
        <th width="15%">A等</th>
        <th width="15%">B等</th>
        <th width="15%">C等</th>
        <th width="15%">D等</th>
        <th width="20%">操作</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item,key) in list">
        <td>
          <p>{{item.courseName}}</p>
        </td>
        <td>
          <p v-show="item.status !== true">{{item.levelA}}</p>
          <el-input-number class="input" v-model="form.levelA"
                           v-show="item.status === true" size="small"
                           controls-position="right" :min="0" :max="1000000"
          ></el-input-number>
        </td>
        <td>
          <p v-show="item.status !== true">{{item.levelB}}</p>
          <el-input-number class="input" v-model="form.levelB"
                           v-show="item.status === true" size="small"
                           controls-position="right" :min="0" :max="1000000"
          ></el-input-number>
        </td>
        <td>
          <p v-show="item.status !== true">{{item.levelC}}</p>
          <el-input-number class="input" v-model="form.levelC"
                           v-show="item.status === true" size="small"
                           controls-position="right" :min="0" :max="1000000"
          ></el-input-number>
        </td>
        <td>
          <p v-show="item.status !== true">{{item.levelD}}</p>
          <el-input-number class="input" v-model="form.levelD"
                           v-show="item.status === true" size="small"
                           controls-position="right" :min="0" :max="1000000"
          ></el-input-number>
        </td>
        <td>
          <span v-show="item.status !== true" @click="showEditFun(item,key)" class="oper">编辑</span>
          <span v-show="item.status === true" @click="editFun(item,key)" class="oper">完成</span>
          <span v-show="item.status === true" @click="clearFun(item,key)"class="oper">取消</span>
        </td>
      </tr></tbody>
    </table>
    </resize-table>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        // 筛选条件
        query:{
          termId: null,
          gradeId: null,
          examBatchId: null
        },
        // 选择的考试批次
        batchModel: [],
        // 所有的考试批次
        courseCascadeList: [],
        // 列表
        list: [],
        form:{
          levelA: 0,
          levelB: 0,
          levelC: 0,
          levelD: 0,
        }
      }
    },
    methods: {
      // 获取学期 年级 考试批次级联
      async getCourseCascade(){
        this.courseCascadeList = await this.$ajax({
          url: '/score/queryTermGradeExamCascade.do'
        })
        this.query.termId = this.courseCascadeList[0].value
        this.query.gradeId = this.courseCascadeList[0].children[0].value
        this.query.examBatchId = this.courseCascadeList[0].children[0].children[0].value
        this.batchModel = [this.query.termId, this.query.gradeId, this.query.examBatchId]
      },
      //
      async getDetail(){
        let query = {
          termId: this.batchModel[0],
          gradeId: this.batchModel[1],
          examBatchId: this.batchModel[2],
        }
        this.list = await this.$ajax({
          url: '/score/queryScoreLevelList.do',
          query
        })
      },
      // 展现编辑表单
      showEditFun(item,key){
        if(this.list.map((e,i)=>{
          if(e.status){
            e.status = false
            this.list.splice(i,1,e)
          }
        }))
        item.status = true
        this.list.splice(key,1,item)
        this.form = {
          id: item.id,
          levelA: item.levelA,
          levelB: item.levelB,
          levelC: item.levelC,
          levelD: item.levelD,
        }
      },
      // 取消修改
      clearFun(item,key){
        item.status = false
        this.list.splice(key,1,item)
      },
      // 修改数据
      async editFun(item,key){
        await this.$ajax({
          url: '/score/updateScoreLevel.do',
          query: this.form
        })
        item.status = false
        this.list.splice(key,1,item)
        this.$remind('修改成功')
        this.getDetail()
      },
    },
    async mounted() {
      await this.getCourseCascade()
      this.getDetail()
    }
  }
</script>
<style lang="sass" scoped>
  .index
    .search-box
      margin-top: 10px
      .item
        display: inline-block
        margin-right: 15px
      .text
        margin-left: 5px
        color: $main-c
        display: block
        margin-bottom: 3px
      .cascader
        width: 320px
</style>
