<template>
  <div class="index">
    <div @click="showDialog()" class="btn-main-deepen">新增</div>
    <search-box :searchData="searchData" :table="table"></search-box>
    <resize-table>
    <table class="table score-table">
      <thead>
      <tr>
        <th width="25%">考试批次名称</th>
        <th width="12%">年级</th>
        <th width="20%">学期</th>
        <th width="13%">考试类型</th>
        <th width="15%">时间</th>
        <th width="15%">操作</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="item in table.list">
        <td>{{item.examDesc}}</td>
        <td>{{getGradeName(item.gradeId)}}</td>
        <td>{{getTermName(item.termId)}}</td>
        <td>{{$enums.ExamType.getName(item.examType)}}</td>
        <td>{{item.examDate | substrTime}}</td>
        <td>
          <span @click="showDialog(item)" class="oper">编辑</span>
          <span @click="showScoreDialog(item)" class="oper">设置单科分数</span>
        </td>
      </tr></tbody>
    </table>
    </resize-table>
    <el-dialog :title="!form.examBatchId ? '新增考试批次': '编辑考试批次'" class="dialog" :visible.sync="dialog" width="540px">
      <div class="item img">
        <span class="title imp">考试类型</span>
        <el-select default-first-option filterable clearable class="input" v-model="form.examType">
          <el-option v-for="item in $enums.ExamType.list"
                     :value="item.value" :key="item.value" :label="item.label"
          ></el-option>
        </el-select>
      </div>
      <div class="item img">
        <span class="title imp">年级</span>
        <el-select default-first-option filterable clearable class="input" v-model="form.gradeId">
          <el-option v-for="item in searchData[1].option"
                     :value="item.value" :key="item.value" :label="item.label"
          ></el-option>
        </el-select>
      </div>
      <div class="item img">
        <span class="title imp">学期</span>
        <el-select default-first-option filterable clearable class="input" v-model="form.termId">
          <el-option v-for="item in searchData[0].option"
                     :value="item.value" :key="item.value" :label="item.label"
          ></el-option>
        </el-select>
      </div>
      <div class="item">
        <span class="title imp">时间</span>
        <el-date-picker
            class="input"
            format="yyyy年MM月"
            :clearable="false"
            v-model="form.examDate"
            type="month"
            placeholder="选择时间">
        </el-date-picker>
      </div>
      <div class="item">
        <span class="title imp">名称</span>
        <el-input placeholder="请输入名称" class="input" v-model="form.examDesc"></el-input>
      </div>
      <div class="btns">
        <div @click="doSubmit" class="btn-submit mr15">提交</div>
        <div class="btn-clear">取消</div>
      </div>
    </el-dialog>
    <el-dialog title="设置单科分数" class="dialog" :visible.sync="scoreDialog" width="540px">
      <p class="batch-name">{{scoreTitle}}</p>
      <resize-table>
      <table class="table score-single-table">
        <thead>
        <tr>
          <th>科目</th>
          <th>满分</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,key) in scoreList">
          <td>{{item.courseName}}</td>
          <td>
            <p v-show="!editScore">{{item.score}}</p>
            <el-input-number
                v-model="setScoreList[key].score"
                v-show="editScore" class="input" size="small"
                controls-position="right" :min="1" :max="100000"
            ></el-input-number>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <div v-show="!editScore" class="btns">
        <div  @click="editScore = true" class="btn-submit mr15">编辑</div>
        <div @click="scoreDialog = false" class="btn-clear">取消</div>
      </div>
      <div v-show="editScore" class="btns">
        <div @click="setScoreFun" class="btn-submit mr15">完成</div>
        <div @click="editScore = false" class="btn-clear">取消</div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        searchData: [
          {type: 'select', title: '学期', width: '200px',  model: 'termId', option: []},
          {type: 'select', title: '年级', model: 'gradeId', option: []},
        ],
        table: {
          url: '/exam/queryExamBatchPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            gradeId: null,
            termId: null,
          }
        },
        dialog: false,
        form: {
          gradeId: null,
          termId: null,
          examDate: null,
          examDesc: '',
          examType: null
        },
        scoreDialog: false,
        // 分数设置列表
        scoreList: [],
        // 编辑分数设置
        editScore: false,
        //
        setScoreList: [],
        scoreTitle: ''
      }
    },
    methods: {
      // 获取学期
      async getTermList() {
        let res = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        this.searchData[0].option = res.map(e => {
          return {label: e.termName, value: e.schoolTermId}
        })
        res.forEach(e=>{
          if(e.isDefault === 'YES'){
            this.query.schoolTermId = e.schoolTermId
          }
        })
      },
      // 获取年级
      async getGradeList() {
        let res = await this.$ajax({
          url: '/grade/queryGradeList.do'
        })
        this.searchData[1].option = res.map(e => {
          return {label: e.name, value: e.id}
        })
      },
      // 根据年级ID获取年级名称
      getGradeName(id) {
        let arr = this.searchData[1].option
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].value === id) {
            return arr[i].label
            break
          }
        }
      },
      // 根据学期ID获取学期名称
      getTermName(id) {
        let arr = this.searchData[0].option
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].value === id) {
            return arr[i].label
            break
          }
        }
      },
      // 展示弹窗
      showDialog(item) {
        if (item) {
          this.form = {
            examBatchId: item.id,
            gradeId: item.gradeId,
            termId: item.termId,
            examDate: item.examDate,
            examDesc: item.examDesc,
            examType: item.examType
          }
        } else {
          this.form = {
            gradeId: null,
            termId: null,
            examDate: null,
            examDesc: '',
            examType: null
          }
        }
        this.dialog = true
      },
      // 提交
      async doSubmit() {
        if (!this.form.gradeId) {
          this.$message.warning('请选择年级')
          return
        }
        if (!this.form.termId) {
          this.$message.warning('请选择学期')
          return
        }
        if (!this.form.examType) {
          this.$message.warning('请选择考试类型')
          return
        }
        if (!this.form.examDate) {
          this.$message.warning('请选择考试时间')
          return
        }
        if (!this.form.examDesc) {
          this.$message.warning('请输入名称')
          return
        }
        let url = '/exam/addExamBatchPage.do'
        let text = '新增成功'
        let query = JSON.parse(JSON.stringify(this.form))
        console.log(this.form.examBatchId)
        if(this.form.examBatchId){
          url = '/exam/updateExamBatch.do'
          text = '修改成功'
        }
        query.examDate = new Date(query.examDate).$Format('yyyy-MM-dd hh:mm:ss')
        await this.$ajax({
          url,
          query,
        })
        this.$remind(text)
        this.dialog = false
        this.$search(this.table)
      },
      // 展示分数弹窗
      async showScoreDialog(item){
        this.editScore = false
        this.scoreTitle = item.examDesc
        this.scoreDialog = true
        // 获取分数设置
        this.scoreList = await this.$ajax({
          url: '/exam/queryExamScoreSet.do',
          el: '.score-single-table',
          query:{
            id: item.id
          }
        })
        this.setScoreList = []
        this.scoreList.forEach(e=>{
          this.setScoreList.push({
            id: e.id,
            examBatchId: e.examBatchId,
            courseId: e.courseId ,
            score: e.score ,
          })
        })

      },
      // 设置单科分数
      async setScoreFun(){
        await this.$ajax({
          url: '/exam/updateExamScoreSet.do',
          query: {
            examScoreSets: this.setScoreList
          }
        })
        this.$remind('设置成功')
        this.scoreDialog = false
        this.$search(this.table)
      }
    },
    mounted() {
      this.getTermList()
      this.getGradeList()
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .index
    .btn-main-deepen
      position: absolute
      top: 24px
      right: 15px
    .dialog
      .batch-name
        font-size: $font-large
        text-align: center
        color: $gray-dark
        margin-bottom: 15px
      .title
        width: 80px
        line-height: 40px
        vertical-align: top
        display: inline-block
      .input
        width: calc(100% - 140px)
      .imp:before
        content: '*'
        color: $error-c
        margin-right: 4px
      .file-input
        /deep/ input
          padding-top: 10px
          line-height: 1
      .item
        margin-bottom: 20px
      .btns
        margin-top: 25px
        text-align: center
    .table
      .input
        width: 200px

</style>
